@import url(https://fonts.googleapis.com/css?family=Proxima+Nova);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
.flexbox {
  position: relative;
  left: 12.3%;
  top: calc(50% - 61px/2);
  display: inline-block;
  justify-content: center;
  align-items: center;
}

.search > div {
  display: inline-block;
  position: relative;
}

.search > div > input {
  position: relative;
  color: #8E8E93;
  font-size: 16px;
  background: transparent;
  width: 25px;
  height: 25px;
  border: solid 1px #007AFF;
  outline: none;
  border-radius: 35px;
  transition: width 0.5s;
  text-indent: 10px;
}

.search > div > input::-webkit-input-placeholder {
  color: #8E8E93;
  opacity: 1;
}

.search > div > input:-ms-input-placeholder {
  color: #8E8E93;
  opacity: 1;
}

.search > div > input::-ms-input-placeholder {
  color: #8E8E93;
  opacity: 1;
}

.search > div > input::placeholder {
  color: #8E8E93;
  opacity: 1;
}

.search > div > input::-ms-placeholder {
  color: #8E8E93;
}

.search > div > input::-ms-input-placeholder {
  color: #8E8E93;
}

.search > div > input:focus,
.search > div > input:valid {
  width: 250px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  background-color: #fff;
  color: #333;
  transform: translateY(0);
  transition: transform 0.3s ease;
  z-index: 10;
}

.header.shadow {
  box-shadow: 0 9px 9px -9px rgba(0, 0, 0, 0.13);
}

.header.hidden {
  transform: translateY(-110%);
}

.logo {
  font-weight: bold;
  position: absolute;
  height: 27px;
  left: 7.47%;
  right: 89.94%;
  top: calc(50% - 27px/2 - 0.5px);

  font-family: 'Proxima Nova';
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.530588px;

  color: #007AFF;
}

.links {
  position: absolute;
  right: 43%;
  bottom: 5%;
}

a {
  text-decoration: none;
  color: inherit;
}

.link-item {
  display: inline;
  position: relative;

  font-family: Proxima Nova;
  font-size: 16px;
  line-height: 20px;
/* identical to box height, or 125% */

  letter-spacing: -0.256px;

