.news-piece-card-header {
	display: flex;
	flex-direction: row;
}

.news-piece-card-title {
	height: 108px;
	padding-left: 20px;
	text-align: left;
	width: 80%;
	align-self: left;
	display: inline-block;
	color: #000;
}

.news-piece-card-time {
	width: 20%;
	right: 0%;
	text-align: right;
	padding-right: 20px;
	display: inline-block;
}

.news-piece-card-content {
	text-align: left;
	padding-left: 24px;
	padding-right: 16px;
	padding-bottom: 35px;
	padding-top: 13px;
	height: 144px;
	top: 144px;
	display: block;
	font-size: 18px;
	line-height: 26px;
	color: #000;
}
.events-card-type {
	position: absolute;
	width: 159px;
	height: 36px;
	border-radius: 15px;
	background: #0579FF;
	text-align: center;
	margin-left: 22px;
	margin-top: 25px;
}

.events-card-type > p {
	color: #fff;
	margin: 0px;
	height: 100%;
	padding: 8px 0;
	vertical-align: middle;
}

.events-card-img {
	width: inherit;
	height: 178px;
	overflow: hidden;
}

.events-card-img > img {
	border-radius: 14px;
	width: 100%;
}

.events-card-header > h3 {
	text-align: left;
	color: #000;
	padding-left: 17px;
	font-size: 17px;
	margin-top: 12px;
	margin-bottom: 0px;
}

.events-card-content > p {
	text-align: left;
	color: #000;
	margin-top: 2px;
	padding-left: 17px;
	font-size: 13px;
}
.email-card-box {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  background-color: #fff;
  margin: 14px;
  height: 270px;
  border-radius: 14px;
}

.email-card-header {
	display: flex;
	flex-direction: row;
}

.email-card-author {
	width: 59%;
}

.email-card-author > p {
	text-align: left;
	margin-top: 17px;
	margin-left: 20px;
	font-size: 14px;

}

.email-card-status-time {
	display: flex;
	flex-direction: row;
	width: 41%;

}

.email-card-status {
	width: 10px;
	height: 10px;
	background: #007AFF;
	border-radius: 100%;
	align-self: center;
	margin-left: auto;
	display: inline-block;
	margin-top: 3px;
}

.email-card-time {
	max-width: 90%;
	margin-left: auto;
	display: inline-block;
}

.email-card-time > p {
	margin-top: 17px;
	margin-right: 20px;
	margin-left: 10px;
	font-size: 14px;
	text-align: right;
}

.email-card-title > h3 {
	margin-left: 20px;
	text-align: left;
	font-size: 16px;
	color: #000;
}

.email-card-content > p {
	max-height: 100px;
	text-align: left;
	margin-left: 20px;
	margin-right: 20px;
	font-size: 16px;
	line-height: 26px;
	color: #000;
	text-overflow: clip;
	overflow: hidden;
}
.cards {
  display: flex;
  color: #8b8b8b;
}

.cards-header {
  width: 50%;
  text-align: left;
  font-size: 28px;
  margin-top: 0px;
  display: inline-block;
}

.cards-header > h2 {
  padding-left: 11px;
}

.cards-link-see-all {
  width: 50%;
  text-align: right;
  font-size: 16px;
  margin-top: 0px;
  color: #007AFF;
  display: inline-block;
}

.cards-link-see-all > a {
  padding-right: 13px;
}

.card-box {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  background-color: #fff;
  margin: 14px;
  height: 313px;
  border-radius: 14px;
}
.page-title {
	text-align: left;
	font-size: 60px;
	padding-left: 10px;
	margin-top: 0px;
}
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: Proxima Nova;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

.wrapper {
  text-align: center;
  height: 100%;
}

.section {
  height: 100%;
  min-height: 2000px;
  background-color: #F2F2F7;
  padding-top: 126px;
  padding-left: 111px;
  padding-right: 111px;
  margin: 0 auto;
  /*padding: 130px 122px 24px;*/
}

.content {
  background-color: #F2F2F7;
  display: flex;
  flex-direction: column;
  /*max-width: 940px;*/
  margin: 0 auto;
  border-radius: 1px;
}

.main {
  display: flex;
  padding: 16px;
  padding-top: 0;
}

.main-left {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  height: 800px;
  margin: 16px;
  color: #8b8b8b;
  background-color: rgb(245, 245, 245);
  border-radius: 3px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

.main-right {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  margin: 16px;
}

.right-box {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  margin: 16px 0;
  color: #8b8b8b;
  background-color: rgb(245, 245, 245);
  border-radius: 3px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

.right-box:first-child {
  margin-top: 0;
}

.right-box:last-child {
  margin-bottom: 0;
}
